.login-container {
  display: flex;
  flex-direction: column; /* 垂直排列内容 */
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f0f2f5;
}

.login-card {
  width: 400px;
  padding: 20px;
  margin-bottom: 20px; /* 给登录框和底部链接之间添加间距 */
}

.login-title {
  text-align: center;
  margin-bottom: 20px;
}

.login-button-container {
  text-align: center;
}

.login-footer {
  text-align: center;
  font-size: 14px;
  margin-top: 3%;
}
</style>
